
import XHeader from 'vux/src/components/x-header/index.vue'

import Scroll from '../../components/scrollAll'
import OrderList from '../../components/order/orderList'

export default {
name: "OrderListOut",
components: {
XHeader,
Scroll,
OrderList
},
data() {
return {
page: 1,
count: 20,
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
}
},
beforeRouteLeave (to, from, next) {
if (to.path == '/') {
from.meta.keepAlive = false;
next();
}else{
next();
}
},
mounted() {

},
methods: {
gotoAddPage(){
this.$router.push('/order')
}
}
}



